a {
  text-decoration: none;
  color: inherit; }

.motherDivFooter {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: repeat(3, 1fr);
  height: 150px;
  width: 100%;
  position: sticky;
  background: #333;
  color: #e4e4e4; }
  .motherDivFooter .nameDiv {
    display: flex;
    align-items: center;
    justify-content: flex-start;
    grid-column: 1;
    grid-row: 1/4; }
    .motherDivFooter .nameDiv .name {
      display: flex;
      justify-content: center;
      align-items: center;
      width: 45%; }
    @media (max-width: 600px) {
      .motherDivFooter .nameDiv .name {
        width: 60%; } }
    @media (max-width: 450px) {
      .motherDivFooter .nameDiv .name {
        width: 85%; } }
  .motherDivFooter .storyDiv {
    grid-column: 2;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .motherDivFooter .storyDiv .story {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 70%;
      width: 40%; }
    @media (max-width: 600px) {
      .motherDivFooter .storyDiv .story {
        width: 60%; } }
  .motherDivFooter .contactDiv {
    grid-column: 2;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .motherDivFooter .contactDiv .contact {
      width: 40%;
      background: #FFE100;
      color: black;
      height: 70%;
      display: flex;
      justify-content: center;
      align-items: center; }
    @media (max-width: 600px) {
      .motherDivFooter .contactDiv .contact {
        width: 60%; } }
  .motherDivFooter .workDiv {
    grid-column: 2;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .motherDivFooter .workDiv .work {
      width: 40%;
      height: 70%;
      display: flex;
      justify-content: center;
      align-items: center; }
    @media (max-width: 600px) {
      .motherDivFooter .workDiv .work {
        width: 60%; } }
