html, body {
  height: 100%; }

.motherDiv {
  display: flex;
  flex-direction: column; }
  .motherDiv .mainDiv {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center; }
    .motherDiv .mainDiv .surroundingDiv {
      width: 100%;
      margin-top: 60px;
      margin-bottom: 200px;
      display: flex;
      justify-content: center;
      align-items: center;
      display: grid;
      grid-template-columns: repeat(6, 1fr);
      grid-template-rows: 200px 100px 200px 150px 150px;
      grid-gap: 4%;
      min-width: 720px;
      max-width: 800px; }
      .motherDiv .mainDiv .surroundingDiv .introBox {
        height: 100%;
        grid-column: 1/5;
        grid-row: 1;
        background: #666;
        color: #e4e4e4;
        display: flex;
        box-shadow: 0 8px 15px rgba(0, 0, 0, 0.5); }
      .motherDiv .mainDiv .surroundingDiv .imageBox {
        height: 100%;
        grid-column: 5/7;
        grid-row: 1;
        background: #666;
        color: #e4e4e4; }
      .motherDiv .mainDiv .surroundingDiv .toolboxHeadBox {
        height: 100%;
        grid-row: 2;
        grid-column: 3/7;
        background: #666;
        color: #e4e4e4; }
      .motherDiv .mainDiv .surroundingDiv .comfortableBox {
        height: 100%;
        grid-column: 1/3;
        grid-row: 2/4;
        background: #666;
        color: #e4e4e4; }
      .motherDiv .mainDiv .surroundingDiv .experienceBox {
        height: 100%;
        grid-column: 3/5;
        grid-row: 3;
        background: #666;
        color: #e4e4e4; }
      .motherDiv .mainDiv .surroundingDiv .toolsBox {
        height: 100%;
        grid-column: 5/7;
        grid-row: 3;
        background: #666;
        color: #e4e4e4; }
      .motherDiv .mainDiv .surroundingDiv .storyBox {
        height: 100%;
        grid-column: 1/7;
        grid-row: 4;
        background: #666;
        color: #e4e4e4; }
      .motherDiv .mainDiv .surroundingDiv .socialBox {
        height: 100%;
        grid-column: 1/7;
        grid-row: 5;
        background: #666;
        color: #e4e4e4; }
    @media (max-width: 850px) {
      .motherDiv .mainDiv .surroundingDiv {
        grid-gap: 2.5%;
        min-width: inherit;
        width: 93%; } }
    @media (max-width: 600px) {
      .motherDiv .mainDiv .surroundingDiv {
        width: 90%;
        grid-template-rows: 200px 250px 100px 200px 150px 150px; }
        .motherDiv .mainDiv .surroundingDiv .introBox {
          grid-column: 1/7;
          grid-row: 1; }
        .motherDiv .mainDiv .surroundingDiv .imageBox {
          grid-column: 1/7;
          grid-row: 2; }
        .motherDiv .mainDiv .surroundingDiv .toolboxHeadBox {
          grid-column: 4/7;
          grid-row: 3; }
        .motherDiv .mainDiv .surroundingDiv .comfortableBox {
          grid-column: 1/4;
          grid-row: 3/5; }
        .motherDiv .mainDiv .surroundingDiv .experienceBox {
          grid-column: 4/7;
          grid-row: 4; }
        .motherDiv .mainDiv .surroundingDiv .toolsBox {
          display: none;
          grid-row: 4; }
        .motherDiv .mainDiv .surroundingDiv .storyBox {
          grid-column: 1/7;
          grid-row: 5; }
        .motherDiv .mainDiv .surroundingDiv .socialBox {
          grid-column: 1/7;
          grid-row: 6; } }
